//for the taps
.tap-bars {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  align-items: end;
  position: relative;
  bottom: 4.2vw;
  grid-gap: 0.8vw;
  padding: {
    inline: 4vw;
    left: 4vw;
    right: 4vw;
  }
  margin-top: 5vw;
}

.each-tap {
  display: grid;
  align-items: end;

  * {
    grid-area: 1/1;
    margin: 0 3px;
    border-radius: 10px;
  }

  .capacity {
    background-color: #262d3b;
  }
  .level {
    background-color: #f7b71e;
    border-radius: 1px 1px 6px 6px;
    margin: {
      inline: 0.5vw;
      block: 0.2vw;
      left: 0.5vw;
      right: 0.5vw;
      bottom: 0.2vw;
    }
  }

  p {
    writing-mode: vertical-lr;
    place-self: start center;
    transform: rotate(180deg);
    padding: {
      inline: 0.5rem;
      top: 0.5rem;
    }
  }
}

.tap-img {
  width: 90%;
  align-items: flex-end;
  margin-bottom: -30px;
}
